"use client"

import { useState } from "react"
import { Search, X } from "lucide-react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import {
  DropdownMenu,
  DropdownMenuCheckboxItem,
  DropdownMenuContent,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Badge } from "@/components/ui/badge"
import type { User } from "@/types/user"

export interface UserFilterState {
  search: string
  roles: string[]
  statuses: User["status"][]
}

interface UserFiltersProps {
  filters: UserFilterState
  onFiltersChange: (filters: UserFilterState) => void
  totalUsers: number
}

const roleOptions = [
  "Спортсмен",
  "Тренер",
  "Администратор",
  "Судья",
]

const statusOptions: User["status"][] = ["active", "inactive", "blocked"]

const statusLabels: Record<User["status"], string> = {
  active: "Активен",
  inactive: "Неактивен",
  blocked: "Заблокирован",
}

export function UserFilters({ filters, onFiltersChange, totalUsers }: UserFiltersProps) {
  const activeFiltersCount = filters.roles.length + filters.statuses.length

  const handleRoleToggle = (role: string) => {
    const newRoles = filters.roles.includes(role)
      ? filters.roles.filter((r) => r !== role)
      : [...filters.roles, role]
    onFiltersChange({ ...filters, roles: newRoles })
  }

  const handleStatusToggle = (status: User["status"]) => {
    const newStatuses = filters.statuses.includes(status)
      ? filters.statuses.filter((s) => s !== status)
      : [...filters.statuses, status]
    onFiltersChange({ ...filters, statuses: newStatuses })
  }

  const handleClearFilters = () => {
    onFiltersChange({
      search: "",
      roles: [],
      statuses: [],
    })
  }

  return (
    <div className="rounded-lg border bg-white p-4">
      <div className="flex items-center gap-3">
        {/* Search */}
        <div className="relative flex-1">
          <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
          <Input
            placeholder="Поиск по ФИО или email..."
            value={filters.search}
            onChange={(e) => onFiltersChange({ ...filters, search: e.target.value })}
            className="pl-9"
          />
        </div>

        {/* Role Filter */}
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="outline" className="gap-2">
              Роль
              {filters.roles.length > 0 && (
                <Badge variant="secondary" className="ml-1 px-1.5 py-0">
                  {filters.roles.length}
                </Badge>
              )}
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end" className="w-[200px]">
            <DropdownMenuLabel>Роль</DropdownMenuLabel>
            <DropdownMenuSeparator />
            {roleOptions.map((role) => (
              <DropdownMenuCheckboxItem
                key={role}
                checked={filters.roles.includes(role)}
                onCheckedChange={() => handleRoleToggle(role)}
              >
                {role}
              </DropdownMenuCheckboxItem>
            ))}
          </DropdownMenuContent>
        </DropdownMenu>

        {/* Status Filter */}
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="outline" className="gap-2">
              Статус
              {filters.statuses.length > 0 && (
                <Badge variant="secondary" className="ml-1 px-1.5 py-0">
                  {filters.statuses.length}
                </Badge>
              )}
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end" className="w-[200px]">
            <DropdownMenuLabel>Статус</DropdownMenuLabel>
            <DropdownMenuSeparator />
            {statusOptions.map((status) => (
              <DropdownMenuCheckboxItem
                key={status}
                checked={filters.statuses.includes(status)}
                onCheckedChange={() => handleStatusToggle(status)}
              >
                {statusLabels[status]}
              </DropdownMenuCheckboxItem>
            ))}
          </DropdownMenuContent>
        </DropdownMenu>

        {/* Clear Filters */}
        {activeFiltersCount > 0 && (
          <Button variant="ghost" size="sm" onClick={handleClearFilters} className="gap-2">
            <X className="h-4 w-4" />
            Сбросить
          </Button>
        )}
      </div>

      {/* Results count */}
      <div className="mt-3 text-sm text-muted-foreground">
        Найдено: <span className="font-medium text-foreground">{totalUsers}</span>
      </div>
    </div>
  )
}
